body {
    font-family: 'Raleway', sans-serif;
    color: rgb(255, 255, 255);
    -webkit-box-sizing: border-box;
     box-sizing: border-box;
}

/* layout */

.flex-row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.flex-col {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.grid {
    height: calc(100vh - 95px);
    min-height: 400px;
}

@media all and (min-width: 768px) {
    .grid {
        height: calc(100vh - 160px);
    }
}

@media all and (min-width: 1200px) {
    .grid {
        height: calc(100vh - 240px);
        min-height: 400px;
    }
}

/* header */

.head {
    position: relative;
    width: 100%;
    height: auto;
    -ms-flex-pack: center;
        justify-content: center;
}

.head > div {
    padding: 0 30px;
}

.month {
    font-family: 'Do Hyeon', sans-serif;
    font-size: 1.75rem;
    width: 480px;
    text-align: center;
}

.arrow-f, .arrow-b {
    cursor: pointer;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

@media all and (min-width: 768px) {

    .head > div {
        padding: 0 50px;
    }
        
    .month {
        font-size: 4rem;
    }

}

@media all and (min-width: 1200px) {

    .month {
        font-size: 6rem;
        width: 580px;
    }

}

/* day headings */

.days {
    width: 100%;
    margin: 20px 0px;
    text-align: center;
}

@media all and (min-width: 768px) {
    .days {
         margin: 30px 0px;
    }
}

@media all and (min-width: 1200px) {
    .days {
        margin: 50px 0px;
   }
}

/* cells */

.weeks {
    width: 100%;
    height: 100%;
}

.cell {
    position: relative;
    display: inline-block;
    -ms-flex: 1 1;
        flex: 1 1;
    text-align: center;
    height: 100%;
    font-size: 1em;
    border-right: solid 1px rgba(255,255,255,0.6);
    border-top: solid 1px rgba(255,255,255,0.6);
    -webkit-transition: 0.25s ease-out;
    -o-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
}

.cell:hover {
    cursor: pointer;
    background-color: rgba(32, 32, 32, 0.4);
    -webkit-transition: 0.25s ease-out;
    -o-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
}

.number {
    position: absolute;
    top: 0.5em;
    right: 0.5em;
}

.today {
    -webkit-box-shadow: inset 0 0 20px 5px rgba(255, 255, 255, 0.404);
            box-shadow: inset 0 0 20px 5px rgba(255, 255, 255, 0.404);
}

.disabled {
    background-color: rgba(255,255,255,0.6);
    pointer-events: none;
}

@media all and (min-width: 768px) {
.cell {
    font-size: 1.25em;
    }
}

@media all and (min-width: 1200px) {
.cell {
    font-size: 1.75em;
    }
}

