  /* text cells */

  .appt {
    display: none;
}

@media all and (min-width: 768px) {

    .appt {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        border: none;
        outline: none;
        resize: none;
        font-family: inherit;
        background-color: rgba(255, 255, 255, 0);
        height: 60%;
        width: 99%;    
        color: #fff;
